import React, { useState, useEffect, Fragment } from 'react'

import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

import AttentionDetail from './AttentionDetail'
import { Button } from '@material-ui/core';

import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import SearchIcon from '@material-ui/icons/Search';
import Slide from '@material-ui/core/Slide';

import Tooltip from '@material-ui/core/Tooltip'
import TablePagination from '@material-ui/core/TablePagination';

import { withStyles } from '@material-ui/core/styles';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';
import { deleteAttention, openAttention, closeAttention, listAttentions } from '../../api/api';
import Chip from '@material-ui/core/Chip';
import { AiOutlineLock, AiOutlineUnlock } from 'react-icons/ai';

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import './attention.css';

const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
    },
    iconWidth: {
        width: 10
    },
    paper: {
        padding: theme.spacing(2),
        marginTop: 10,
    },
    button: {
        margin: theme.spacing(1),
    },
    input: {
        display: 'none',
    },
    fab: {
        margin: theme.spacing(1),
    },
    extendedIcon: {
        marginRight: theme.spacing(1),
    },
    icon: {
        marginRight: theme.spacing(1),
        color: 'black'
    },
}))

const styles = (theme) => ({
    root: {
        margin: 0,
        padding: theme.spacing(2),
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    }
});

const DialogTitle = withStyles(styles)((props) => {
    const { children, classes, onClose, ...other } = props;
    return (
        <MuiDialogTitle disableTypography className={classes.root} {...other}>
            <Typography variant="h6">{children}</Typography>
            {onClose ? (
                <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
                    <CloseIcon />
                </IconButton>
            ) : null}
        </MuiDialogTitle>
    );
});

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
})

const Person = _ => {

    useEffect(() => {
        let data = { start: 0, end: rowsPerPage, paginate: true }
        listAttentions(data).then(response => {
            setAttentions(response.attentions);
            setTotalAttentions(response.count);
        });
    }, []);

    const classes = useStyles()

    const [attentions, setAttentions] = useState([])
    const [attention, setAttention] = useState(null)
    const [totalAttentions, setTotalAttentions] = useState(0)
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(10);

    const [open, setOpen] = useState(false);
    const [openDetailAttention, setOpenDetailAttention] = useState(false);

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
        let data = { start: newPage, end: rowsPerPage, paginate: true }
        listAttentions(data).then(response => {
            setAttentions(response.attentions);
            setTotalAttentions(response.count);
        });
    }
    const handleChangeRowsPerPage = event => {

        setRowsPerPage(+event.target.value);
        setPage(0);
        let data = { start: 0, end: event.target.value, paginate: true }
        listAttentions(data).then(response => {
            setAttentions(response.attentions);
            setTotalAttentions(response.count);
        });
    }

    const onCloseDeleteAttention = () => {
        setOpen(false);
    }

    const onDeleteAttention = id => {
        setOpen(false);

        deleteAttention(id).then(response => {
            if (response.status === 'success') {
                toast.success(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
                const newAttentions = attentions.map((currentAttention) => {
                    if (id !== currentAttention._id)
                        return currentAttention

                    currentAttention.status = "deleted";
                    return currentAttention
                })
                setAttentions(newAttentions)
            } else {
                toast.error(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
            }
        }).catch(e => {

            toast.error('Ocurrió un error al eliminar la atención', {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        })

    }


    const onDetailAttention = attention => () => {
        setAttention(attention)
        setOpenDetailAttention(true)
    }
    const onOpenAttention = attention => () => {
        openAttention(attention._id).then(response => {
            if (response.status === 'success') {

                toast.success(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });

                const newAttentions = attentions.map((currentAttention) => {
                    if (attention._id !== currentAttention._id)
                        return currentAttention

                    currentAttention.status = "created";
                    return currentAttention
                })
                setAttentions(newAttentions)
            } else {
                toast.error(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
            }
        }).catch(e => {
            console.log(e);
            toast.error("Occurrió un error al abrir la atención", {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        });
    }
    const onCloseAttention = attention => () => {
        closeAttention(attention._id).then(response => {
            if (response.status === 'success') {

                toast.info(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
                const newAttentions = attentions.map((currentAttention) => {
                    if (attention._id !== currentAttention._id)
                        return currentAttention

                    currentAttention.status = "finished";
                    return currentAttention
                })
                setAttentions(newAttentions)
            } else {
                toast.error(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
            }
        }).catch(e => {
            console.log(e);
            toast.error("Occurrió un error al cerrar la atención", {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        });
    }

    const closeApu = () => {
        setOpenDetailAttention(false)
    }


    return (
        <Fragment>
            <ToastContainer />
            <Paper className={classes.paper}>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell>NOMBRE</TableCell>
                            <TableCell>DESCRIPCIÓN</TableCell>
                            <TableCell>ESTADO</TableCell>
                            <TableCell>CLIENTE</TableCell>
                            <TableCell>VER</TableCell>
                            <TableCell>ABRIR/CERRAR</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {
                            attentions.map(item => {
                                return (
                                    <TableRow key={item._id}>
                                        <TableCell>{item.title} </TableCell>
                                        <TableCell>{item.description} </TableCell>
                                        <TableCell>
                                            {
                                                item.status === 'finished' ?
                                                    <Chip label='Cerrado' variant="outlined" /> :
                                                    <Chip label='Abierto' color="primary" variant="outlined" />
                                            }
                                        </TableCell>
                                        <TableCell>{item.customer.name}</TableCell>

                                        <TableCell className={classes.iconWidth}>
                                            <Tooltip title="Detalle" placement="top">
                                                <SearchIcon className={classes.icon} onClick={onDetailAttention(item)} />
                                            </Tooltip>
                                        </TableCell>


                                        <TableCell className={classes.iconWidth}>
                                            {
                                                item.status === 'finished' ?
                                                    <div className='wrapper-icon-attention'>
                                                        <AiOutlineUnlock className='icon' onClick={onOpenAttention(item)} />
                                                        <span className='open-close'>Abrir</span>
                                                    </div> :
                                                    <div className='wrapper-icon-attention'>
                                                        <AiOutlineLock className='icon' onClick={onCloseAttention(item)} />
                                                        <div className='open-close'>Cerrar</div>
                                                    </div>
                                            }
                                        </TableCell>
                                    </TableRow>)
                            })
                        }
                    </TableBody>
                </Table>
                <TablePagination
                    rowsPerPageOptions={[10, 25, 100]}
                    component="div"
                    count={totalAttentions}
                    rowsPerPage={rowsPerPage}
                    page={page}
                    onChangePage={handleChangePage}
                    onChangeRowsPerPage={handleChangeRowsPerPage}
                />
            </Paper>


            <Dialog
                open={open}
                onClose={onCloseDeleteAttention}
                aria-labelledby="draggable-dialog-title">
                <DialogTitle style={{ cursor: 'move' }} id="draggable-dialog-title">
                    Alerta
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Esta seguro de eliminar esta atención?
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseDeleteAttention} color="primary">
                        NO
                    </Button>
                    <Button onClick={onDeleteAttention} color="primary">
                        SI
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog
                fullWidth maxWidth="lg"
                open={openDetailAttention}
                onClose={closeApu}
                TransitionComponent={Transition}>
                <DialogContent>
                    <AttentionDetail attention={attention} />
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={closeApu} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>

        </Fragment>)
}

export default Person